import { useState, useMemo } from "react";
import "./styles.css";

export default function App() {
  const [people, setPeople] = useState(8);
  const [pieces, setPieces] = useState(3.75);

  const [calcCm, calcRounded] = useMemo(() => {
    const cmPerPiece = 25 / 6; // 6 pieces per 25cm
    const totalPieces = people * pieces;
    const totalCm = Math.round(cmPerPiece * totalPieces);
    const order = Math.ceil(totalCm / 25) * 25;

    return [totalCm, order];
  }, [people, pieces]);

  return (
    <div className="App">
      <h3>DerciCalc</h3>
      <table>
        <tr>
          <td>People:</td>
          <td>
            <input
              type="number"
              defaultValue={people}
              onChange={(e) => setPeople(e.target.value)}
            ></input>
          </td>
        </tr>
        <tr>
          <td>Pieces per person:</td>
          <td>
            <input
              type="number"
              defaultValue={pieces}
              onChange={(e) => setPieces(e.target.value)}
            ></input>
          </td>
        </tr>
        <tr>
          <th>Required Length:</th>
          <td>{calcCm}cm</td>
        </tr>
        <tr>
          <th>Required Order:</th>
          <td>{calcRounded}cm</td>
        </tr>
      </table>
    </div>
  );
}
